import _ from 'lodash';
import React from 'react';
import {Link} from 'react-router-dom';
import logo from './img/test-logo.png';


let courseLink = [];
const Navbar = (props)=>{

 
  
    props.data.forEach((course,index)=>{
    if(index <3){
     courseLink.push(course.name);
    }
    
  })
  console.log(courseLink.length);

    return(
        <nav className="navbar  container navbar-expand-lg navbar-dark bg-dark">
  <a className="navbar-brand" rel="noopener noreferrer" target="_blank" href="https://github.com/AftanGithub">
      <img src={logo} alt="logo" className="logo"/>
  </a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto">
      <li className="nav-item active">
      <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span> </Link>
      </li>
      <li className="nav-item dropdown">
        <Link className="nav-link dropdown-toggle" to="/" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Dropdown
        </Link>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {courseLink.map((courseName,index)=>{
            if(index <3){
              return <Link key={index} className="dropdown-item" to={`/courses/${_.replace(_.toLower(courseName)," ","-")}`} >{courseName}</Link>
            }else{
              return null;
            }
          })} 
          <div className="dropdown-divider"></div>
          <Link className="dropdown-item" to="/courses">More</Link>
        </div>
      </li>
     
    </ul>
    
  </div>
</nav>
    )
}

export default Navbar;




