import React from 'react';
import './SingleCourse.css';

const SingleCourse = (props)=>{
    
    return(
        
        <div className="container">

            <div className="row">
                <div className="col-md-6 col-sm-12 img-section2">
                    <div className="overlay"></div>
                    <img src={props.src} className="course-img" alt={props.name}></img>
       
                </div>
                <div className="col-md-6 col-sm-12" >
                    <div className="info-card">
                        
                           
                    <ul className="nav nav-tabs">
                    <li className="active">
                        <a data-toggle="tab" href="#instructor"> 
                        <i class="fa fa-user-circle-o" aria-hidden="true"></i>
                        Instructor
                        </a>
                    </li>
                    <li >
                        <a data-toggle="tab" href="#details"> 
                            <i class="fa fa-info-circle" aria-hidden="true"></i>
                            Details
                        </a>
                    </li>
                    <li>
                        <a data-toggle="tab" href="#duration">
                        <i class="fa fa-clock-o" aria-hidden="true"></i>
                        Duration
                        </a>
                    </li>
                    
                    </ul>

                    <div className="tab-content text-dark">
                    <div id="instructor" className="tab-pane fade in active show">
                        <h3>Instructor</h3>
                        <div>
                        <img className="instructor" src={props.instructorPic} alt={props.instructor}/>
                        </div>
                        <div>
                        <p className="text-secondary ml-2">
                        {props.instructor}
                        </p> 
                        <p className="text-secondary mt-1">
                            <small>
                                {props.about}
                            </small>
                        </p>
                        </div>
                    </div>
                    <div id="details" className="tab-pane fade ">
                        <h3>Details</h3>
                        <p className="text-secondary " >{props.details}</p>
                    </div>
                    <div id="duration" className="tab-pane fade">
                        <h3>Duration</h3>
                        <p className="text-secondary">{props.duration} Months</p>
                    </div>
                   
                    </div>
                            
                        </div>
                    </div>
                </div>
           
        </div>
    )
}

export default SingleCourse;


