import React from 'react';
import { Link } from 'react-router-dom';
import front from './img/front-img.jpg';
import './Landing.css';
const Landing = ()=>{
    return(
        <div className="container bg-dark">
            <div className="row">
                <div className="col-md-6 col-sm-12 data-section">
                    <div className="heading">
                       <h1 className="display-4">Earn Your Skills For Better Career</h1> 
                    </div>
                    <div className="sub">
                    <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                    Pellentesque vulputate mi nisi, quis semper leo semper ut. 
                    Aenean eu magna vitae dui elementum ultrices.
                    </p> 
                    </div>
                    <div className="btn-section">
                        <Link to="/courses" className="btn-orange">View Courses</Link>
                    </div>
                </div>
                <div className="col-md-6 col-sm-12 img-section">
                    <div>
                        <img src={front} alt="woman" className="front"/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Landing;


