import React,{useEffect,useState} from 'react';
import { courseData } from '../../content/courseData';
import _ from 'lodash';
import SingleCourse from './SingleCourse/SingleCourse';

const LoadProduct= ({match})=>{
const [name,setName] = useState("");
const [duration,setDuration] = useState("");
const [details,setDetails] = useState("");
const [imgSrc,setImgSrc] = useState("");
const [instructor,setInstructor] = useState("");
const [instructorPic,setInstructorPic] = useState("");
const [aboutInstructor,setAboutInstructor] = useState("");
const {slug} = match.params;

useEffect(()=>{
    loadCourse(courseData);
},[slug])

const loadCourse = (coursesArr)=>{

    


    
       let foundCourse = courseData.filter((c)=>{
           return _.replace(_.toLower(c.name)," ","-")===slug;
        })

        setName(foundCourse[0].name);
        setDetails(foundCourse[0].details);
        setDuration(foundCourse[0].duration);
        setImgSrc(foundCourse[0].src);
        setInstructor(foundCourse[0].instructor);
        setInstructorPic(foundCourse[0].instructorPic);
        setAboutInstructor(foundCourse[0].aboutInstructor);

       


    // foundCourse = coursesArr.find(findCourse);
    
}
return(
    <div className="text-white">
      <SingleCourse 
      name={name}
      duration={duration} 
      details={details} 
      src={imgSrc}
      instructor={instructor}
      instructorPic={instructorPic}
      about={aboutInstructor}
      />
    </div>
)

}   

export default LoadProduct;


