import React from 'react';
import { Link } from 'react-router-dom';
// import web from './img/web.jpg';
import './Course.css';
import _ from 'lodash';
const Course = (props)=>{
   const courseURL = _.replace(_.toLower(props.courseName)," ","-");
   
   
    return(
        <div className="card bg-dark m-3">
            
            <img className="card-img-top" src={props.imgSrc} alt={props.courseName} />
            <div className="card-body">
            <h5 className="card-title title">{props.courseName}</h5>
            <p className="card-text">{props.details}</p>
            <div className="mt-5">
            <Link to={`/courses/${courseURL}`} className="view-btn">View Course</Link>
            </div>
            </div>
        </div>
    )
}


export default Course;


